import Typography from 'typography';

import 'typeface-news-cycle';

const typography = new Typography({
  baseFontSize: '20px',
  baseLineHeight: 1,
  headerFontFamily: ['News Cycle', 'Indie Flower', 'Montserrat', 'Avenir Next', 'Helvetica Neue', 'Segoe UI', 'Helvetica', 'Arial', 'sans-serif'],
  bodyFontFamily: ['Segoe UI', 'Georgia', 'serif'],
});

export default typography



